$secondary: #f3f3f3;
@import "../../utilities/variable/variables.scss";

$smallPhoneSatu: 500px;

@mixin smallPhone {
  @media (max-width : #{$smallPhoneSatu}) {
    @content;
  }
}

$device1100: 1100px;
$device1300: 1300px;

@mixin device1300 {
  @media (min-width: #{$device1100}) and (max-width: #{$device1300}) {
    @content;
  }
}

$deviceMin1300: 1300px;
$deviceMax1500: 1500px;

@mixin device1500 {
  @media (min-width: #{$deviceMin1300}) and (max-width: #{$deviceMax1500}) {
    @content;
  }
}

// min width 951 and max width 1360px
$responMin951: 951px;
$responMax1360: 1360px;

@mixin respon1360 {
  @media (min-width: #{$responMin951}) and (max-width: #{$responMax1360}) {
    @content;
  }
}
// end min width 951 and max width 1360px

// Min widht 750 and Max width 950
$responMin750: 750px;
$responMax950: 950px;
// END Min widht 750 and Max width 950

// Min width 750 and max width 950
@mixin respon950 {
  @media (min-width: #{$responMin750}) and (max-width: #{$responMax950}) {
    @content;
  }
}
// END Min widht 750 and max width 950

// Min widht 500 and Max width 750
$respon750: 750px;
$respon500: 500px;
// END Min widht 500 and Max width 750

// Min widht 500 and Max width 750
@mixin respon750 {
  @media (min-width : #{$respon500}) and (max-width: #{$respon750}) {
    @content;
  }
}
// END Min widht 500 and Max width 750

// For Position Hover ALL Pulau
.containerMapsNusantara {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .container-position-pulau-inMaps {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 370px;
    width: 1000px;
    justify-content: center;
    position: relative;

    @include respon1360() {
      height: 260px;
    }

    @include respon950() {
      height: 260px;
      width: 700px;
    }

    @include respon750() {
      height: 185px;
      width: 500px;
    }

    @include smallPhone() {
      width: 100vw;
      height: 20vh;
    }

    #mapsDefault {
      z-index: -1;
    }

    .imgMaps {
      position: absolute;
      object-fit: cover;
      object-position: center;
      height: auto;
      width: 100%;
      // cursor: pointer;

      @include respon1360() {
        height: auto;
        width: 700px;
      }

      @include respon950() {
        height: auto;
        width: 700px;
      }

      @include respon750() {
        width: 500px;
      }

      @include smallPhone() {
        height: auto;
        width: 100%;
      }
      // @include smallPhoneDua() {
      //     height: 20vh;
      // }
    }

    // Target Mouse 1
