@import "../../../utilities/variable/variables.scss";
$smallPhoneSatu: 500px;
$smallPhoneDua: 400px;

@mixin smallPhone {
  @media (max-width : #{$smallPhoneSatu}) {
    @content;
  }
}

@mixin smallPhoneDua {
  @media (max-width : #{$smallPhoneDua}) {
    @content;
  }
}

// min width 1361px and max width 1440px
$responMin1361: 1361px;
$responMax1440: 1440px;

@mixin respon1440 {
  @media (min-width: #{$responMin1361}) and (max-width: #{$responMax1440}) {
    @content;
  }
}
// end min width 1361px and max width 1440px

// min width 951 and max width 1360px
$responMin951: 951px;
$responMax1360: 1360px;

@mixin respon1360 {
  @media (min-width: #{$responMin951}) and (max-width: #{$responMax1360}) {
    @content;
  }
}
// end min width 951 and max width 1360px

// Min widht 750 and Max width 950
$responMin750: 750px;
$responMax950: 950px;
// END Min widht 750 and Max width 950

// Min width 750 and max width 950
@mixin respon950 {
  @media (min-width: #{$responMin750}) and (max-width: #{$responMax950}) {
    @content;
  }
}
// END Min widht 750 and max width 950

// Min widht 500 and Max width 750
$respon750: 750px;

$respon500: 500px;
// END Min widht 500 and Max width 750

// Min widht 500 and Max width 750
@mixin respon750 {
  @media (min-width : #{$respon500}) and (max-width: #{$respon750}) {
    @content;
  }
}
// END Min widht 500 and Max width 750

.wrapperDetail {
  @include smallPhone() {
    padding: 0 0 20px 0;
  }
  // START HEADERS
  .detailHead {
    background: url(../../../img/kampanye.jpg);
    background-size: cover;
    height: 15vh;
    background-position: center;
    @include smallPhone() {
      width: auto;
      margin-left: 0;
      padding: 0 10%;
    }

    h2 {
      @include smallPhoneDua() {
        font-size: 15pt;
      }
    }
  }
}
// END HEADERS
// START LINK PARTAI
.linkedDetail {
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-top: 20px;
  padding: 40px 10%;
  @include smallPhone() {
    // border: 1px solid crimson;
    width: 100%;
    padding: $paddingMobile;
  }
}
// END LINK PARTAI

.pengurus {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0 12%;
  margin-bottom: 2rem;
  @include smallPhone() {
    padding: 0 10px;
  }
  .bidangTextHead {
    margin: 2rem 0;
    text-align: center;
    font-weight: bold;
    color: #000000;
    @include smallPhone() {
      margin: 1rem 0;
    }
  }
  .profilPengurus {
    display: flex;
    // flex-direction: column;
    width: 100%;
    height: auto;
    min-height: 100vh;
    padding: 0;
    // border: 1px solid blue;
    @include smallPhone() {
      padding: $paddingMobile;
      flex-direction: column;
      height: auto;
    }

    .kolum1 {
      // width: 40%;
      height: 100%;
      display: flex;
      align-items: center;
      flex-direction: column;
      margin-top: 20px;
      padding: $paddingTiny;

      @include respon950() {
        padding: 10px 5%;
      }

      @include respon750() {
        padding: 10px 5%;
      }

      @include smallPhone() {
        align-items: center;
        width: 100%;
      }
      img {
        height: 200px;
        width: 100%;
        object-fit: cover;
        object-position: center;
        @include smallPhone() {
          width: 180px;
        }
      }

      .textProfil {
        display: flex;
        flex-direction: column;
        position: static;
        width: 100%;
        margin-top: 20px;
        @include smallPhone() {
          justify-content: center;
          align-items: center;
          flex-direction: column;
          padding: 0 20%;

          @include respon1440() {
            padding: 0 10%;
          }

          @include respon1360() {
            padding: 0 5%;
          }

          @include respon950() {
            padding: 0 2%;
          }

          @include respon750() {
            padding: 0 15%;
          }

          @include smallPhone() {
            padding: 0 10px;
          }
          .bidangTextHead {
            margin: 2rem 0;
            text-align: center;
          }

          .profilText {
            height: auto;
            margin: 10px 10px 10px 0;
            @include smallPhone() {
              margin: 5px;
            }

            small {
              color: $colors;
              // font-size: 8pt;
            }
            .namaProfil {
              text-transform: uppercase;
            }
            h6 {
              text-align: start;
              font-weight: bold;

              @include smallPhone() {
                text-align: center;
              }
            }
          }
        }
      }
      .kolum2 {
        display: flex;
        // justify-content: center;
        // align-items: center;
        flex-direction: column;
        width: 80%;
        height: 100%;
        margin: 0 0 0 30px;
        // border: 1px solid blue;

        @include smallPhone() {
          width: 100%;
          margin: 40px 0 0 0px;
          padding: 0px;
        }

        .descProfil {
          display: flex;
          flex-direction: column;
          margin-bottom: 1rem;
          @include smallPhone() {
            text-align: center;
          }

          .biografi {
            text-transform: uppercase;
          }

          p {
            margin-top: 1rem;
            // font-size: 10pt;
            @include smallPhone() {
              text-align: start;
            }
          }

          .descBidang {
            font-weight: bold;
            margin-top: 1.5rem;
          }
        }
      }
    }
    // .borderBottom {
    //     width: 10%;
    //     height: 5px;
    //     background-color: $colors;
    //     position: absolute;
    //     // top: 50%;
    //     left: 50%;
    //     transform: translate(-50%, -50%);
    // }
  }
}
