$smallPhoneSatu: 500px;
$smallPhoneDua: 400px;
@mixin smallPhone {
  @media (max-width : #{$smallPhoneSatu}) {
    @content;
  }
}
@mixin smallPhoneDua {
  @media (max-width : #{$smallPhoneDua}) {
    @content;
  }
}

// min width 1361px and max width 1440px
$responMin1361: 1361px;
$responMax1440: 1440px;

@mixin respon1440 {
  @media (min-width: #{$responMin1361}) and (max-width: #{$responMax1440}) {
    @content;
  }
}
// end min width 1361px and max width 1440px

// min width 951 and max width 1360px
$responMin951: 951px;
$responMax1360: 1360px;

@mixin respon1360 {
  @media (min-width: #{$responMin951}) and (max-width: #{$responMax1360}) {
    @content;
  }
}
// end min width 951 and max width 1360px

// min width 951 and max width 1360px
$responMin1200: 1200px;
$responMax1360: 1360px;

@mixin respon1200 {
  @media (min-width: #{$responMin1200}) and (max-width: #{$responMax1360}) {
    @content;
  }
}
// end min width 951 and max width 1360px

// Min widht 750 and Max width 950
$responMin750: 750px;
$responMax950: 950px;
// END Min widht 750 and Max width 950

// Min width 750 and max width 950
@mixin respon950 {
  @media (min-width: #{$responMin750}) and (max-width: #{$responMax950}) {
    @content;
  }
}
// END Min widht 750 and max width 950

// Min widht 500 and Max width 750
$respon750: 750px;
$respon500: 500px;
// END Min widht 500 and Max width 750

// Min widht 500 and Max width 750
@mixin respon750 {
  @media (min-width : #{$respon500}) and (max-width: #{$respon750}) {
    @content;
  }
}
// END Min widht 500 and Max width 750

@import "../../../utilities/variable/variables.scss";

$secondary: #f3f3f3;

.wrapperBeritaPage1 {
  width: 100%;

  .maps {
    padding: 0 20%;
    @include respon1200() {
      padding: 0 10%;
    }
  }

  @include smallPhone() {
    padding: 0 0 40px 0;
  }

  .headers-BeritaPage1 {
    background: url(../../../img/detailartikel1.jpg);
    background-size: cover;
    height: 15vh;
    background-position: center;

    @include smallPhone() {
      height: 15vh;
      background-attachment: inherit;
    }

    .backgrounds {
      background-color: rgba(255, 0, 8, 0.1);
      width: 100%;
      height: 100%;
      display: flex;
      // justify-content: center;
      align-items: center;
      color: #ffffff;

      .textBackgroundBerita {
        width: 30%;
        height: auto;
        margin-left: 13rem;

        @include smallPhone() {
          width: auto;
          margin-left: 0;
          padding: 0 10%;
        }

        .txt-backg-beritaPage1 {
          @include smallPhone() {
            font-size: 12pt;
          }
        }
      }
    }
  }
  .linkedBeritaPage1 {
    display: flex;
    align-items: center;
    flex-direction: row;
    width: 100%;
    margin-top: 15px;
    padding: 40px 10%;

    @include respon1360() {
      padding: 30px 5%;
    }

    @include respon950() {
      padding: 40px 5%;
    }

    @include respon750() {
      padding: 10px 5%;
    }

    @include smallPhone() {
      width: 100%;
      padding: 0 2%;
    }

    i {
      span {
        margin-left: 5px;
        font-style: italic;
        font-size: 8pt;
      }
    }
  }

  // Container1

  // END Container1

  .col1-section6-beritaPage1 {
    display: flex;
    height: 20vh;
    width: 100%;
    background: #f3f3f3;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    padding: 20px 20% 0 20%;

    @include smallPhone() {
      height: auto;
      padding: 30px 10px 20px 10px;
    }

    .col-title-berita-terbaru-beritaPage1 {
      display: flex;
      height: auto;
      width: 100%;
      justify-content: center;
      align-items: center;
      overflow: hidden;

      .txt-berita-terbaru {
        display: flex;
        font-size: 15px;
        font-weight: bold;
        color: #d80010;
        margin: 1px;
        overflow: hidden;
      }
    }

    .col-garis-lurus {
      display: flex;
      height: 1px;
      width: 100%;
      background: #ccc;
      justify-content: center;
      align-items: center;
      margin: 5px 0 0 0;
      overflow: hidden;
    }
  }

  .col2-section6-beritaPage1 {
    width: 100%;
    // height: 60vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    margin-top: 0;
    padding: 0 20% 20px 20%;

    @include smallPhone() {
      flex-direction: column;
      height: auto;
      padding: 0 10px 0px 10px;
    }
  }
}
