$animationSpeed: 40s;

// Animation
@keyframes scroll {
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-250px * 14))}
}


// Styling
.slider {
	height: 100px;
	margin: auto;
	overflow:hidden;
	position: relative;
	
	
	
	&::after {
		right: 0;
		top: 0;
		transform: rotateZ(180deg);
	}

	&::before {
		left: 0;
		top: 0;
	}
	
	.slide-track {
		animation: scroll $animationSpeed linear infinite;
		display: flex;
		width: calc(250px * 14);
		gap: 10rem;
	}
	
	.slide {
		height: 100px;
        width: 250px;
        .hovercom {
			-webkit-filter: grayscale(100%);
			filter: grayscale(100%);
			opacity: 0.5;
			transition: .5s;

			&:hover {
				-webkit-filter: grayscale(0);
				filter: grayscale(0);
				opacity: 1;
		
			}
		}
	}
}